
.customize-message-container {
  display: flex;
  flex-direction: column;
  height: calc(100vh - 1.5rem);
  padding: 0.75rem;
}

.customize-message-header {
  display: flex;
  justify-content: space-evenly;
  height: 100%;
}

.customize-message-header h4 {
  margin-left: 1rem;
}

.form-container {
  display: flex;
  flex-direction: column;
  margin: 1rem 0 1rem 0;
  min-width: 50%;
  height: 100%;
  width: 50vw;
  align-self: center;
}

.input-fields-container {
  display: flex;
  flex-direction: column;
}

img {
  height: 350px;
  width: 450px;
}

h4 {
  margin: 3vh 0 3vh 0;
}

input {
  outline: none;
  box-shadow: 2px 2px 5px gray;
  border-color: white;
  border-style: none;
  padding: 0.5rem;
  margin: 0.5rem 0 0.5rem 0;
}

#input-field-body {
  outline: none;
  resize: none;
  box-shadow: 2px 2px 5px gray;
  border-color: white;
  padding: 0.5rem;
  margin: 0.5rem 0 0.5rem 0;
}

@media screen and (max-width: 800px) {
  .customize-message-container {
    flex-direction: column;
  }
  .customize-message-header {
    flex-shrink: 6;
  }

  .form-container {
    width: 100%;
  }
  .input-fields-container {
    flex-grow: 1;
  }
  .form-container {
    height: 100%;
    flex-direction: column;
    justify-content: space-between;
  }
  .customize-message-header img {
    height: auto;
    width: 150px;
    align-self: flex-start;
  }
}